import React from "react";
import CredentialsPage from "./credentials_ui.jsx";
import LoginPage from "./login_ui.jsx";
import
{
    EpicIcon
} from "./common_ui.jsx";

import
{
    refreshAllCredentialsAsync,
    startServerAsync,
    loadAllCredentialsAsync,
    endAllServersAsync,
} from "./ipc_renderer.js";

class StartServerPage extends React.Component 
{
    state =
    {
        error: null,
        portString: ""
    };

    portErrorMessages =
    {
        "EADDRINUSE": "Port is already in use"
    };

    notifySuccess = (server, port) =>
    {
        localStorage.setItem("port", this.state.portString);

        if (typeof this.props.onSuccess === "function")
        {
            this.props.onSuccess(server, port);
        }
    }

    start = () =>
    {
        return new Promise((resolve, reject) =>
        {
            var port = parseInt(this.state.portString, 10);
            if (port.toString() !== this.state.portString) 
            {
                return reject(new Error("Failed to parse port"));
            }
    
            return startServerAsync(port)
            .then((server) =>
            {
                console.log(`Server started on port ${port}`);
                this.notifySuccess(server, port);
                
                return resolve();
            })
            .catch((error) =>
            {
                return reject(error);
            })
        })
        .catch((error) =>
        {
            if ("code" in error && error.code in this.portErrorMessages)
            {
                this.setState(
                {
                    error: new Error(this.portErrorMessages[error.code])
                });
            }
            else if (parseInt(this.state.portString, 10) < 1024 || parseInt(this.state.portString, 10) > 65535)
            {
                this.setState(
                {
                    error: new Error("Port must be a number between 1024-65535")
                });
            }
            else
            {
                // This is a hack to extract the correct error, because ipcRenderer.invoke can't return data in custom Errors
                for (const key in this.portErrorMessages)
                {
                    if (error.message.includes(key))
                    {                            
                        this.setState(
                        {
                            error: new Error(this.portErrorMessages[key])
                        });
                        return;                     
                    }
                }

                this.setState(
                {
                    error: error
                });
            }
        });
    };
    
    onPortKeyDown = (event) =>
    {
        if (event.key === "Enter")
        {
            this.start();
        }
    }

    onPortChange = (event) =>
    {
        this.setState(
        {
            portString: event.target.value,
            error: null
        });
    }

    onStartClick = (event) =>
    {
        this.start();
    }

    componentDidMount()
    {
        this.setState(
        {
            portString: localStorage.getItem("port"),
        });

        endAllServersAsync()
    }

    render()
    {
        return (
            <div className="container-center">
                <div className="hero">
                    <EpicIcon />
                </div>
                <div className="section">
                    <span>Enter a port to serve developer authorization.</span>
                    <div className="section-input flex">
                        <input
                            className={`${this.state.error ? "error" : ""}`}
                            autoFocus
                            placeholder="Port"
                            onKeyDown={this.onPortKeyDown}
                            onChange={this.onPortChange}
                            value={this.state.portString || ""}
                            />

                        <button onClick={this.onStartClick}>Start</button>
                    </div>

                    <div className="error-tooltip">{this.state.error ? this.state.error.message : ""}</div>
                </div>
            </div>
        )
    }
}

class ServerListeningPage extends React.Component
{
    state =
    {
    };

    refreshInterval = null;

    componentDidMount()
    {
        loadAllCredentialsAsync();

        // Every minute, attempt to refresh credentials
        this.refreshInterval = setInterval(function()
        {
            refreshAllCredentialsAsync()
            .catch((error) =>
            {
                console.log(error);
            });
        }, 60000)
    }

    componentWillUnmount()
    {
        clearInterval(this.refreshInterval);

        if (this.props.server)
        {
            console.log("Server shutting down");
            this.props.server.close();
        }
    }

    render()
    {
        return (
            <div className="container-stretch flex">
                <div className="container-panel fixed container-stretch flex">
                    <CredentialsPage port={this.props.port} />
                </div>
                <div className="container-panel">
                </div>
                <LoginPage />
            </div>
        )
    }
}

export default class RootPage extends React.Component
{
    state =
    {
        step: 0,
        server: null,
        port: 0
    };

    onSuccess = (server, port) =>
    {
        this.setState(
        {
            step: this.state.step + 1,
            server,
            port
        });
    };

    rendererSteps =
    [
        () =>
            <StartServerPage
                onSuccess={this.onSuccess}
            />,
        () =>
            <ServerListeningPage
                server={this.state.server}
                port={this.state.port}
            />
    ];

    render()
    {
        return (
            <div className="container-stretch flex">
                {this.rendererSteps[this.state.step]()}
            </div>
        )
    }
}