import React from "react";

import
{
    getEnvironmentAsync,
    clearCookiesAsync,
    addCredentialAsync,
    getCredentialsByAccountIdAsync,
    requestAuthTokenInfoForAuthCodeAsync,
} from "./ipc_renderer.js";

class FallbackPage extends React.Component
{
    state =
    {
    }

    notifyStartLoginRequested = () =>
    {
        if (typeof this.props.onStartLoginRequested === "function")
        {
            this.props.onStartLoginRequested();
        }
    }

    onLoginClick = (event) =>
    {
        this.notifyStartLoginRequested();
    }

    componentDidMount()
    {
        if (this.props.error)
        {
            clearCookiesAsync();
        }
    }

    render()
    {
        return (
            <div className="container-center">
                <h1>Now serving.</h1>
                <div className="section">
                    <span>Login to add a credential.</span>
                    <div className="section-input">
                        <button onClick={this.onLoginClick}>Login</button>
                    </div>
                    <div className="error-tooltip">{this.props.error ? this.props.error.message : ""}</div>
                </div>
            </div>
        );
    }
}

class AccountPortalPage extends React.Component 
{
    state =
    {
        url: null
    }

    constructor(props)
    {
        super(props);
        this.webview = React.createRef();
    }

    notifyAuthCodeReceived = (authCode) =>
    {
        if (typeof this.props.onAuthCodeReceived === "function")
        {
            this.props.onAuthCodeReceived(authCode);
        }
    }

    notifyErrorReceived = (error) =>
    {
        if (typeof this.props.onErrorReceived === "function")
        {
            this.props.onErrorReceived(error);
        }
    }

    componentDidMount()
    {
        const thisComponent = this;

        this.webview.current.addEventListener("did-fail-load", function(event)
        {
            thisComponent.notifyErrorReceived(new Error(`Failed to load the login page. ${event.errorDescription}`));
        });

        this.webview.current.addEventListener("will-navigate", function(event)
        {
            const authCodeUrlPrefix = "https://www.epicgames.com/eossdkauth?code=";
            if (event.url.startsWith(authCodeUrlPrefix))
            {
                stop();

                const authCode = event.url.substr(authCodeUrlPrefix.length);
                thisComponent.notifyAuthCodeReceived(authCode);
            }
            else if (event.url.startsWith("https://www.epicgames.com/eossdkauth?error=cancelled"))
            {
                stop();
                
                thisComponent.notifyErrorReceived(new Error("The login was cancelled."));
            }
        });

        getEnvironmentAsync()
        .then((environment) =>
        {
            this.webview.current.src = environment.accountPortalUrl;
        });
    }

    render()
    {
        return (
            <div className="container-stretch">
                <webview ref={this.webview} allowpopups="false" />
            </div>
        );   
    }    
}

class GetAuthTokenInfoPage extends React.Component
{
    notifyAuthTokenInfoReceived = (authTokenInfo) =>
    {
        if (typeof this.props.onAuthTokenInfoReceived === "function")
        {
            this.props.onAuthTokenInfoReceived(authTokenInfo);
        }
    }

    notifyErrorReceived = (error) =>
    {
        if (typeof this.props.onErrorReceived === "function")
        {
            this.props.onErrorReceived(error);
        }
    }

    componentDidMount()
    {
        requestAuthTokenInfoForAuthCodeAsync(this.props.authCode)
        .then((authTokenInfo) =>
        {
            // If there's a credential with this account id already, we can't go any further
            getCredentialsByAccountIdAsync(authTokenInfo.account_id)
            .then((credentials) =>
            {
                if (credentials.length == 0)
                {
                    this.notifyAuthTokenInfoReceived(authTokenInfo);
                }
                else
                {
                    this.notifyErrorReceived(new Error("A credential was already added for that account."));
                }
            })
            .catch((error) =>
            {
                this.notifyErrorReceived(error);
            });
        })
        .catch((error) =>
        {
            // Try to parse a more friendly error message
            if (error.message.includes("{"))
            {
                let errorContent = error.message.substring(error.message.indexOf("{"), error.message.length);
                errorContent = errorContent.substring(0, errorContent.lastIndexOf("}") + 1);
                errorContent = errorContent.replace(/\\n/g, "").replace(/  /g, " ").replace(/\\"/g, "\"");

                try
                {
                    let errorData = JSON.parse(errorContent);

                    if ("errorMessage" in errorData)
                    {
                        let errorMessage = errorData.errorMessage;

                        if ("errorCode" in errorData)
                        {
                            errorMessage = `${errorMessage} (${errorData.errorCode})`;
                        }

                        error = new Error(errorMessage);
                    }
                }
                catch (error) {}
            }

            this.notifyErrorReceived(error);
        });
    }

    render() {
        return <div></div>;
    }
}

class AddCredentialPage extends React.Component 
{
    state =
    {
        inputName: "",
        inputNameError: null
    };

    notifyCredentialAdded = () =>
    {
        if (typeof this.props.onCredentialAdded === "function")
        {
            this.props.onCredentialAdded();
        }
    };

    notifyErrorReceived = (error) =>
    {
        if (typeof this.props.onErrorReceived === "function")
        {
            this.props.onErrorReceived(error);
        }
    };

    add = () =>
    {
        if (this.state.inputName.length == 0)
        {
            this.setState(
            {
                inputNameError: new Error("Name cannot be empty")
            });

            return;
        }

        addCredentialAsync(this.state.inputName, this.props.authTokenInfo)
        .then(() =>
        {
            this.notifyCredentialAdded();
        })
        .catch(error =>
        {
            this.notifyErrorReceived(error);
        })
    };

    onNameKeyDown = (event) =>
    {
        if (event.key === "Enter")
        {
            this.add();
        }
    };

    onNameChange = (event) =>
    {
        this.setState(
        {
            inputName: event.target.value,
            inputNameError: null
        })
    };

    onAddClick = (event) =>
    {
        this.add();
    };
    
    render()
    {
        return (
            <div className="container-center">
                <h1>Hi, {this.props.authTokenInfo.displayName}.</h1>
                <div className="section">
                    <span>Enter a name for your credential.</span>
                    <div className="section-input flex">
                        <input
                            className={`${this.state.inputNameError ? "error" : ""}`}
                            autoFocus
                            placeholder="Name"
                            onKeyDown={this.onNameKeyDown}
                            onChange={this.onNameChange}
                            />

                        <button onClick={this.onAddClick}>Add</button>
                    </div>

                    <div className="error-tooltip">{this.state.inputNameError ? this.state.inputNameError.message : ""}</div>
                </div>
            </div>           
        );
    }
}

export default class LoginPage extends React.Component 
{
    state =
    {
        step: 0,
        error: null,
    };

    onAuthCodeReceived = (authCode) =>
    {
        this.setState(
        {
            step: this.state.step + 1,
            authCode,
        });
    };

    onErrorReceived = (error) =>
    {
        this.setState(
        {
            step: 0,
            error: error
        });
    };

    onAuthTokenInfoReceived = (authTokenInfo) =>
    {
        this.setState(
        {
            step: this.state.step + 1,
            authTokenInfo,
        });
    };

    onCredentialAdded = () =>
    {
        this.setState(
        {
            step: 0,
            error: null,
        });
    };

    onStartLoginRequested = () =>
    {
        clearCookiesAsync();

        this.setState(
        {
            step: 1,
            error: null,
        });
    };

    onBackClick = () =>
    {
        this.setState(
        {
            step: 0,
            error: null,
        });
    }

    rendererSteps =
    [
        () =>
            <FallbackPage
                onStartLoginRequested={this.onStartLoginRequested}
                error={this.state.error}
            />,
        () =>
            <AccountPortalPage
                onAuthCodeReceived={this.onAuthCodeReceived}
                onErrorReceived={this.onErrorReceived}
            />,
        () =>
            <GetAuthTokenInfoPage
                onAuthTokenInfoReceived={this.onAuthTokenInfoReceived}
                onErrorReceived={this.onErrorReceived}
                authCode={this.state.authCode}
            />,
        () =>
            <AddCredentialPage
                onCredentialAdded={this.onCredentialAdded}
                onErrorReceived={this.onErrorReceived}
                authTokenInfo={this.state.authTokenInfo}
            />,
    ];

    render()
    {
        // This silly work-around is because the forge maker improperly handles encoding in a packaged build.
        const decodeHtmlEntities = (function()
        {
            const dummyElement = document.createElement('div');
            return function(text)
            {
                dummyElement.innerHTML = text;
                const output = dummyElement.textContent;
                dummyElement.textContent = '';
                return output;
            }
        })();

        const BackText = decodeHtmlEntities('&#x2C2; Back');
        return (
            <div className="container-stretch flex overflow">
                {this.rendererSteps[this.state.step]()}
                {
                    this.state.step ? 
                    <button
                        className="page-back"
                        onClick={this.onBackClick}
                    >
                        {BackText}
                    </button>
                    : null
                }
            </div>
        );
    }
}