import React from "react";

import
{
    removeCredentialAsync,
    addCredentialsChangedHandlerAsync,
    removeCredentialsChangedHandlerAsync,
} from "./ipc_renderer.js";

const epochs =
[
    [ "year", 31536000 ],
    [ "month", 2592000 ],
    [ "day", 86400 ],
    [ "hour", 3600 ],
    [ "minute", 60 ],
    [ "second", 1 ]
];

function getMostSignificantTime(timeAgoInSeconds)
{
    for (const [name, seconds] of epochs)
    {
        const interval = Math.floor(timeAgoInSeconds / seconds);
        if (interval >= 1)
        {
            return {
                name,
                interval,
            };
        }
    }

    return null;
};

function getTimeAgo(date)
{
    const timeAgoInSeconds = Math.floor((new Date() - new Date(date)) / 1000);

    const mostSignificantTime = getMostSignificantTime(timeAgoInSeconds);
    if (mostSignificantTime)
    {
        const suffix = mostSignificantTime.interval === 1 ? "" : "s";
        return `${mostSignificantTime.interval} ${mostSignificantTime.name}${suffix} ago`;
    }

    return "just now";
}

class CredentialLastUsedTime extends React.PureComponent
{
    state =
    {
        lastUsedTime: null,
        currentTime: null,
    }

    updateInterval = null;

    componentDidMount()
    {
        if (this.updateInterval == null)
        {
            this.updateInterval = setInterval(() =>
            {
                this.setState(
                {
                    lastUsedTime: this.props.credential.lastUsedTime,
                    currentTime: new Date(),
                });
            }, 1000);
        }
    }

    componentWillUnmount()
    {
        if (this.updateInterval)
        {
            clearInterval(this.updateInterval);
            this.updateInterval = null;
        }
    }

    render()
    {
        var lastUsed = "Never used";
        if (this.state.lastUsedTime)
        {
            lastUsed = `Last used ${getTimeAgo(this.state.lastUsedTime)}`;
        }

        return (
            <div className="last-used">
                {lastUsed}
            </div>
        );
    }
}

class Credential extends React.PureComponent
{
    onDeleteClick = (event) =>
    {
        if (typeof this.props.onRemoveCredentialRequested === "function")
        {
            this.props.onRemoveCredentialRequested(this.props.credential);
        }
    };

    render()
    {
        return (
            <div className={`credential`}>
                <div>
                    <div className="name allow-select">{this.props.credential.name}</div>
                    <div className="displayName allow-select">{this.props.credential.authTokenInfo.displayName}</div>
                    <div className="accountId allow-select">{this.props.credential.authTokenInfo.account_id}</div>
                </div>
                <div className="flex" style={{marginTop: "8px"}}>
                    <div className="container-credential-info">
                        <CredentialLastUsedTime credential={this.props.credential} />
                    </div>
                    <div>
                        <button className="red" onClick={this.onDeleteClick}>Delete</button>
                    </div>
                </div>
            </div>
        );
    }
}

export default class CredentialsPage extends React.PureComponent
{
    state =
    {
        credentials: []
    }

    credentialsChanged = (credentials) =>
    {
        this.setState(
        {
            // Create a shallow copy
            credentials: [...credentials]
        });
    }

    onRemoveCredentialRequested = (credential) =>
    {
        removeCredentialAsync(credential.name);
    };

    componentDidMount()
    {
        addCredentialsChangedHandlerAsync(this.credentialsChanged);
    }

    componentWillUnmount()
    {
        removeCredentialsChangedHandlerAsync(this.credentialsChanged);
    }

    render() {
        var credentialsList = (<div>No credentials added.</div>);

        if (this.state.credentials.length > 0)
        {
            credentialsList = this.state.credentials.map((credential) =>
            {
               return <Credential onRemoveCredentialRequested={this.onRemoveCredentialRequested} credential={credential} key={`${credential.name}`} />;
            })
        }

        return (
            <div className="container-stretch info-container">
                <div className="info-title"><div>Host</div></div>
                <div className="info-section">
                    <div className="allow-select">localhost:{this.props.port}</div>
                </div>

                <div className="info-title"><div>Credentials</div></div>
                <div className="info-section">
                    {credentialsList}
                </div>
            </div>
        );
    }
}